"use client"
import React from 'react'
import './workoutPage.css'

const page = () => {
  const [workout, setWorkout] = React.useState<any>(null)

  const getworkout = async () => {
    let data: any = {
       type: 'chest',
        imageUrl: 'https://images.unsplash.com/photo-1600037827534-5770c468049b?w=600&auto=format&fit=crop&q=60&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxzZWFyY2h8MTJ8fHdvcmtvdXQlMjBmb3IlMjBnaXJsJTIwY2hlc3R8ZW58MHx8MHx8fDA%3D',
        durationInMin: 30,
        exercises: [
          {
            exercises: 'Archer Push-up',
            videoUrl: 'https://gymvisual.com/img/p/2/0/9/4/8/20948.gif',
            sets: 3,
            reps: 10,
            rest: 60,
            description: 'Raising both arms above the head, bend the elbows, and interlate the fingers behind the head.'
          },

          {
            exercises: 'Above Head Chest Stretch (female)',
            videoUrl: 'https://gymvisual.com/img/p/2/1/7/4/7/21747.gif',
            sets: 3,
            reps: 10,
            rest: 60,
            description: 'Raising both arms above the head, bend the elbows, and interlate the fingers behind the head.'
          },

           {
            exercises: 'Arm Crossover',
            videoUrl: 'https://gymvisual.com/img/p/2/3/7/9/3/23793.gif',
            sets: 3,
            reps: 10,
            rest: 60,
            description: 'Extend both arms straight in front of your shoulders, with palms facing each other.'
          },

        ]
    }

    setWorkout(data)
  }

  React.useEffect(() => {
    getworkout()
  }, [])

  return (
    <div className='workout'>
      <h1 className='mainhead1'> {workout?.type} Day</h1>
      <div className='workout__excercises'>
        {
          workout?.exercises.map((item: any, index: number) =>{
            return (
              <div className={
                index % 2 === 0 ? 'workout__excercise' : 'workout__excercise workout__excercise--reverse'
              }>

                <h3>{index+1}</h3>
                <div className='workout__excercise__image'>
                  <img src={item.videoUrl}alt="video"/>
                </div>
                <div className='workout__exercise__content'>
                  <h2>{item.exercises}</h2>
                  <span>{item.sets} sets X {item.reps} reps</span>
                  <p>{item.description}</p>
                </div>

              </div>
            )
          })
        }
      </div>
    </div>
  )
}

export default page